'use client';

import { useState } from 'react';

export default function CreatePollForm() {
  const [question, setQuestion] = useState('');
  const [responseCount, setResponseCount] = useState('');
  const [option, setOption] = useState('');
  const [options, setOptions] = useState<string[]>([]);

  const handleAddItem = () => {
    if (!option) return;

    setOptions([...options,  option ]);

    setOption('')
  };

  const handleCreatePoll = async () => {
    if (!question || options.some(opt => !opt.trim())) {
      return;
    }

    try {
      const response = await createPoll({
        question,
        options: options.map(text => ({ text })),
      });
      console.log('Created poll:', response);
      setQuestion('');
      setOption('');
      setResponseCount('');
      setOptions([]);
    } catch (error: any) {
      console.error(error);
    }
  };

  return (
    <div className="max-w-md mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Add Item</h1>
      <div className="space-y-2">
        <label>Question</label>
        <input
          className="w-full p-2 border rounded"
          placeholder="Question"
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
        />

        <label>Response Count</label>
        <input
          className="w-full p-2 border rounded"
          placeholder="Response Count"
          value={responseCount}
          onChange={(e) => setResponseCount(e.target.value)}
        />

        <label>Options</label>
        <input
          className="w-full p-2 border rounded"
          placeholder="Add Option"
          value={option}
          onChange={(e) => setOption(e.target.value)}
        />
        <button
          className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700"
          onClick={handleAddItem}
        >
          Add to List
        </button>
      </div>

      <ul className="mt-6 space-y-2">
        {options.map((item, index) => (
          <li key={index} className="p-2 bg-gray-100">
            {index+1} - {item}
          </li>
        ))}
      </ul>

      <br></br>

      <button
          className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700"
          onClick={handleAddItem}
        >
          Create Poll
        </button>
    </div>
  );
}